<template>
  <div class="search-view">
    <h1>Search</h1>
    <div class="search-container">
      <input 
        v-model="searchQuery" 
        type="text" 
        placeholder="Search pages..." 
        class="search-input"
        @input="performSearch"
      />
      <div v-if="searchResults.length > 0" class="search-results">
        <h3>Search Results</h3>
        <div v-for="result in searchResults" :key="result.filename" class="result-item">
          <h4>{{ result.title }}</h4>
          <p class="category">{{ result.category }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PagesService from '@/services/pagesService'

export default {
  name: 'Search',
  data() {
    return {
      searchQuery: '',
      searchResults: [],
      allPages: []
    }
  },
  async mounted() {
    this.allPages = await PagesService.getAllPages()
  },
  methods: {
    performSearch() {
      if (this.searchQuery.trim()) {
        this.searchResults = PagesService.searchPages(this.allPages, this.searchQuery)
      } else {
        this.searchResults = []
      }
    }
  }
}
</script>

